
#editor {
    min-height: 150px;
    font-size: 25px;
    padding: 10px;
}

#editorContainer {
    color: white;
    border: 1px solid white;
    margin-top: 40px;
}

#btnRow {
    text-align: center;
    justify-content: center;
    margin-top: 10px;
    font-size: 30px;
    color: white;
}

#btnRow a {
    color: white;
    text-decoration: none;
}

#btnRow span{
    margin-left: 10px;
    cursor: pointer;
}